<template>
	<view class="page">
		<tab-slide :tabBars="tabBars" :tabIndex="tabId" @tabtap="tabtap"></tab-slide>
		<view class="uni-tab-bar">
			<swiper 
				class="swiper-box" 
				:style="{height:swiperheight+'px'}" 
				@change="tabChange"
				:current="tabId ">
				<swiper-item v-for="(items, index) in activity" :key="index">
					<scroll-view scroll-y class="list">
						<template v-if="items.acCl.length > 0">
							<view class="acticities" v-for="(item,index1) in items.acCl" :key="index1">
								<text> 有害垃圾</text>
							</view>
						</template>
					</scroll-view>
				</swiper-item>
			</swiper>
		</view>
	</view>
</template>

<script>
import tabSlide from '../../components/tab-slide/tab-slide.vue';
export default {
	components: {
		tabSlide
	},
	data() {
		return {
			swiperheight:500,//高度
			tabId: 0,
			tabBars: [{},{},{}, {},{}],
			activity:[
				{acCl:[]},
				{acCl:[]},
				{acCl:[]},
				{acCl:[]},
				{acCl:[]}
			],
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success:(res) => {
				let height = res.windowHeight-uni.upx2px(100)
				this.swiperHeight = height
			}
		});
	},
	methods: {
		tabtap(index){
			this.tabId = index;
		},
		tabChange(e){
			this.tabId = e.detail.current;
		}
	},
};
</script>

